import React from 'react';
import { motion, AnimatePresence } from "framer-motion";
import { ChakraProvider, ColorModeProvider, useColorMode,useColorModeValue, Button } from "@chakra-ui/react";
import { SunIcon, MoonIcon } from "@chakra-ui/icons";
import {
    Modal,
    ModalOverlay,
    ModalContent,
    ModalHeader,
    ModalFooter,
    ModalBody,
    ModalCloseButton,
    FormControl,
    FormLabel,
    useDisclosure,
    Input,
    Avatar,
    AvatarBadge,
Stack
  } from '@chakra-ui/react'

function InitialFocus() {
    const { isOpen, onOpen, onClose } = useDisclosure()
  
    const initialRef = React.useRef(null)

    return (
      <>
        <button onClick={onOpen} className="plusaddf">+</button>

  
        <Modal
          initialFocusRef={initialRef}
          isOpen={isOpen}
          onClose={onClose}
          isCentered
          size="xs"
        >
          <ModalOverlay />
          <ModalContent>
            <ModalHeader>Search User</ModalHeader>
            <ModalCloseButton />
            <ModalBody pb={6}>
              <FormControl>
                <FormLabel>Username</FormLabel>
                <Input ref={initialRef} placeholder='Enter Username' />
              </FormControl>
  

            </ModalBody>
  
            <ModalFooter>
              <Button colorScheme='blue' mr={3}>
                Start Message
              </Button>
              <Button onClick={onClose}>Cancel</Button>
            </ModalFooter>
          </ModalContent>
        </Modal>
      </>
    )
  }
const MotionButton = motion(Button);
function DarkModeToggle() {
  const { colorMode, toggleColorMode } = useColorMode();


  const bgColor = useColorModeValue('#ebebeb', '#151723');

  return (


   <Stack bg={bgColor} className="sidebar">
        <div class="users-list">
    <Avatar className="userlistimg">
        <AvatarBadge boxSize='1.25em' bg='green.500' />
    </Avatar>

    {/* You can also change the borderColor and bg of the badge */}
    <Avatar className="userlistimg">
        <AvatarBadge bg='tomato' boxSize='1.25em' />
    </Avatar>

        </div>


        <InitialFocus/>


     
    
   </Stack>
  );
}
const SideBar = () => (
    <DarkModeToggle />
);

export default SideBar;