import logo from './logo.svg';
import './App.css';
import { ChakraProvider, ColorModeProvider, useColorMode, Button } from "@chakra-ui/react";
import { SunIcon, MoonIcon } from "@chakra-ui/icons";
import { extendTheme } from "@chakra-ui/react";
import { motion, AnimatePresence } from "framer-motion";
import NavBar from './utils/navbar'; // Import the Loader component
import SideBar from './utils/sidebar'; // Import the Loader component
import ChatBox from './utils/chatbox'; // Import the Loader component



const theme = extendTheme({
  config: {
    initialColorMode: "light",
    useSystemColorMode: false,
  },
});

const MotionButton = motion(Button);
function DarkModeToggle() {
  const { colorMode, toggleColorMode } = useColorMode();

  return (
    <AnimatePresence>
      <Button
        key={colorMode}
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
        onClick={toggleColorMode}
      >
        {colorMode === "light" ? <MoonIcon /> : <SunIcon />}
        
      </Button>
    </AnimatePresence>
  );
}

const Loader = () => (
  <div className="loader">
    {/* Add your loader content here */}
    Loading...
  </div>
);

function App() {
  return (
    <ChakraProvider theme={theme}>
      <ColorModeProvider options={{}}>
        <div class="main">
          <SideBar/>
        <div class="inner-main">
        <NavBar/>
        <div class="inner-main-content">
          <ChatBox/>
        </div>
          </div>
        {/* <Content/> */}
          </div>
      </ColorModeProvider>
    </ChakraProvider>
  );
}

export default App;
